.avatar-container {
  display: inline-block;
  border-radius: 50%;
  -webkit-clip-path: circle(50%);
  clip-path: circle(50%); }
  .avatar-container .avatar__image {
    display: block;
    opacity: 0.75;
    -webkit-transition: opacity 250ms, -webkit-transform 500ms ease-out;
    transition: opacity 250ms, -webkit-transform 500ms ease-out;
    -o-transition: opacity 250ms, transform 500ms ease-out;
    transition: opacity 250ms, transform 500ms ease-out;
    transition: opacity 250ms, transform 500ms ease-out, -webkit-transform 500ms ease-out; }



